html,
body {
  height: 100%; }

html {
  scroll-behavior: smooth; }

body {
  position: relative; }

.section {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; }

h2 {
  font-family: "Catcafe";
  font-size: 36px;
  color: #2B5F8C;
  text-align: center;
  padding: 0 20px; }

@font-face {
  font-family: "Catcafe";
  src: url("../fonts/catcafe.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.butterfly {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative; }
  .butterfly svg {
    display: block;
    margin: 0 auto;
    width: 100%;
    height: 100%;
    padding: 10px; }
    .butterfly svg .areaForColoring:hover {
      cursor: url("/img/icon-color-fill.png") 50 32, auto; }

.reset-btn {
  width: 24px;
  height: 24px;
  background-image: url("/img/icon-reset.png");
  opacity: 0;
  visibility: hidden;
  border: none;
  background-color: transparent;
  position: absolute;
  top: 10%;
  right: 0px;
  transition: all 0.5s ease-in;
  cursor: pointer; }

.color {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  align-items: center;
  justify-content: center;
  padding: 0 20px; }
  .color .color__box {
    width: 60px;
    height: 60px; }
    .color .color__box_01 {
      background-color: #ff6666; }
    .color .color__box_02 {
      background-color: #ffcc66; }
    .color .color__box_03 {
      background-color: #cc9933; }
    .color .color__box_04 {
      background-color: #cccccc; }
    .color .color__box_05 {
      background-color: #663333; }
    .color .color__box_06 {
      background-color: #4691B7; }
    .color .color__box_07 {
      background-color: #797294; }
    .color .color__box_08 {
      background-color: #B887A3; }
    .color .color__box_09 {
      background-color: #848C45; }
    .color .color__box_10 {
      background-color: #3EB595; }
    .color .color__box#color-active {
      border: 2px solid #555555;
      transform: scale(120%); }
    .color .color__box:hover {
      cursor: url("/img/icon-color-dropper.png") 0 50, auto; }
